
import { Component, Vue } from 'vue-property-decorator'
import { IComponentItem, IModalOptions } from '@/components/interface/InterfaceAsyncModal'

@Component
/**
 *   AsyncModal
 * @param {Boolean} isOpen -    
 * @param {Boolean} isOpenChild -       
 * @param {Boolean} isOpenOverlay -   
 * @param {Boolean} isOpenContent -   
 * @param {Boolean} isOpenContentAnimate -     isOpenContent
 *
 * @param {Object} modalOptions -    
 * @param {Array} componentsList -     
 */
export default class MixinsState extends Vue {
  isOpen = false
  isOpenChild = false
  isOpenOverlay = false
  isOpenContent = false
  isOpenContentAnimate = false

  modalOptions: IModalOptions = {}
  componentsList: IComponentItem[] = []

  promiseReturn: any = null

  destroyFull: boolean | null = false
}

